.nav {
  /* width: 20%; */
  /* background-color: #dce1e6; */
  min-height: 100%;
  display: flex;
  flex-direction: column;
  padding: 30px 10px;
  box-sizing: border-box;
}

.text {
  text-decoration: none !important;
}

.item {
  text-decoration: none !important;
  font-size: 13px;
}

.block {
  display: flex;
  align-items: center;
  text-align: center;
  vertical-align: middle;
  border-radius: 5px;
  transition: 0.9s;
  padding: 3.5px;
}

.itemDark {
  color: #e1e3e6;
  text-decoration: none !important;
}

.activeDark {
  border-radius: 5px;
  text-decoration: none !important;
  color: #e1e3e6;
  background-color: rgba(187, 196, 207, 0.19);
}

.itemLight {
  border-radius: 5px;
  text-decoration: none !important;
  color: #000000;
}

.activeLight {
  border-radius: 5px;
  text-decoration: none !important;
  color: #000000;
  background-color: rgba(187, 196, 207, 0.19);
}

.item:hover {
  border-radius: 5px;
  background-color: rgba(174, 183, 194, 0.19);
  border-radius: 8px;
}

.icon {
  height: 17px;
  width: 17px;
  padding-bottom: 2px;
  padding-right: 10px;
}

